@import (less) "color.less";

.accordion-arrow {
    cursor: pointer;
    font-size: 1.6em;
}

.container-row {
    display: table-row;
}

.container-ident-cliente {
    margin-top: 0;
    margin-bottom: 0;
    font-size: 0.875em;
    color: inherit;
    overflow: hidden;
    white-space:nowrap;
    text-overflow:ellipsis;
    width:78%;
    padding-left: 0.625em;
    font-weight: normal;
    float: left;
}
.voce-ident-cliente {
    margin-top: 0;
    margin-bottom: 0;
    font-size: 0.875em;
    color: inherit;
    overflow: hidden;
    white-space:nowrap;
    text-overflow:ellipsis;
    font-weight: normal;
}
.testo-ident-cliente {
    margin-top: 0;
    margin-bottom: 0;
    font-size: 0.875em;
    color: inherit;
    overflow: hidden;
    white-space:nowrap;
    text-overflow:ellipsis;
    font-weight: bold;
}

.panel-body {
    vertical-align: middle;
    
    display: table-cell;
    cursor: pointer;
    text-align: left;
    /*font-weight: bold;*/
}

.info-cliente-open {
    position: relative;
    background-color: @darkest_gray;
    z-index: 90;
    border: 0;
    border-radius: 0;
    text-align: center;
    margin-top: 0.5625em;
    display: table;
                
}
.container-arrow {
    float: right;
    padding-top: 1.25em;
    padding-right: 1.5625em;
            }

.triangolo {
    width: 0;
    height: 0;
    z-index: 90;
    border-left   : 0.563em solid transparent;
    border-right  : 0.563em solid transparent;
    border-bottom : 0.563em solid @darkest_gray;
    float: right;
    margin-right: 1.75em;
}

.triangolo-hide {
    width    : 0;
    height   : 0;
    z-index  : 90;

    border-left   : 0.563em solid transparent;
    border-right  : 0.563em solid transparent;
    border-bottom : 0.563em solid transparent;
    float: right;
    margin-right: 1.75em;
}